body{
    background-color: #fff;
}
.shop-nav{
    @include public(auto);
    .nav-top{
        width: 1230px;
        height: 40px;
        border-top: 1px solid #eee;
        margin-bottom: 10px;
        .nav-top-left{
            float: left;
            height: 40px;
            width: 500px;
            padding-left: 20px;
            line-height: 40px;
            position: relative;
            .text{
                font-size: 14px;
                color: gray;
                margin-right: 15px;
            }
            input{
                width: 200px;
                padding-right: 30px;
                height: 18px;
                text-indent: 10px;
                font-size: 12px;
                outline: none;
            }
            .iconfont{
                color: gray;
                position: absolute;
                left: 330px;
                top:2px
            }
        }
        .nav-top-right{
            float: right;
            height: 40px;
            width: 200px;
            font-size: 13px;
            color: gray;
            text-align: center;
            line-height: 40px;
            span{
                margin: 0 10px;
                font-weight: bold;
            }
        }
    }
    .nav-bottom{
        width: 1230px;
        height: 80px;//220
        border: 2px solid #ccc;
        overflow: hidden;
        .nav-bottom-left{
            width: 120px;
            height: 78px;//218
            background-color: #eee;
            float: left;
            border: 1px solid #ccc;
            border-right: none;
        }
        .nav-bottom-right{
            .nav-init-top{
                height: 30px;
                width: 1109px;
                float: right;
                .init-left{
                    height: 30px;
                    float: left;
                    position: relative;
                    margin-left: 20px;
                    margin-top: 3px;
                    input{
                        width: 100px;
                        padding-right: 30px;
                        height: 17px;
                        text-indent: 10px;
                        font-size: 12px;
                        border: 1px solid #eee;
                        outline: none;
                        box-shadow:1px 1px 1px #eee inset,-1px -1px 1px #eee inset;
                        text-indent: 24px;
                    }
                    span{
                        color: gray;
                        font-size: 14px;
                        position: absolute;
                        left: 7px;
                        top: 5px;
                        font-weight: bold;
                    }
                   
                }
                .more{
                    display: block;
                    width: 50px;
                    height: 30px;
                    float: right;
                    color: gray;
                    line-height: 30px;
                    font-size: 14px;
                    cursor: pointer;
                    &:hover{
                        color:red;
                    }
                }
            }
            .nav-init-botttom{
                height: 170px;
                padding: 0 20px;
                span{
                    display: inline-block;
                    color: gray;
                    font-size: 13px;
                    margin-left: 20px;
                    margin-bottom: 5px;
                    cursor: pointer;
                    &:hover{
                        color:red;
                    }
                }
            }
            
        }
    }
}
.suspend{
    @include public(40px);
    background-color: #eee;
    margin-top: 20px;
    border: 1px solid #ccc;
    ul{
        li{
            height: 40px;
            width: 50px;
            border-right: 1px solid #ccc;
            text-align: center;
            font-size: 13px;
            line-height: 40px;
            float: left;
            cursor: pointer;
            &:hover{
                color: red;
            }
        }
    }
    p{
        height: 40px;
        width: 50px;
        border-right: 1px solid #ccc;
        text-align: center;
        font-size: 13px;
        line-height: 40px;
        float: left;
        cursor: pointer;
        &:hover{
            color: red;
        }
    }
    .susright{
        width: 200px;
        height: 40px;
        line-height: 40px;
        float: right;
        text-align: right;
        span{
            font-size: 18px;
            color: red;
            margin-right: 10px;
            font-weight: bolder;
        }
    }
}
.goodsList{
    @include public(auto);
    width: 1280px;
    margin-top: 20px;
    padding-bottom: 10px;
    margin-bottom: 20px;
    overflow: hidden;
    .goodsUl{
        width: 1300px;
        height: auto;
        margin-left: -6px;
        li{
            width: 210px;
            padding: 5px 5px 0 5px;
            height: 320px;
            float: left;
            margin-left: 31px;
            margin-top: 10px;
            border: 1px solid #ccc;
            &:hover{
                outline: solid red 4px;
            }
            img{
                width: 210px;
            }
            .price{
                color: red;
                font-size: 20px;
                height: 30px;
                line-height: 30px;
            }
            .title{
                height: 20px;
                font-size: 12px;
                line-height: 20px;
                color: #cc6600;
                overflow: hidden;
            }
            .shopname{
                height: 20px;
                font-size: 12px;
                color: #999;
                line-height: 20px;
                text-decoration: underline;
            }
            .goodsLi-bottom{
                height: 3px;
                width: 210px;
                border-top: 1px solid #ccc;
                margin-top: 4px;
            }
            .left{
                height: 32px;
                width: 85px;
                border-right: 1px solid #ccc;
                font-size: 12px;
                float: left;
                line-height: 32px;
                text-indent: 5px;
                span{
                    color: #999;
                    margin-right: 5px;
                }
                b{
                    color:#b57c5b;
                    font-weight: bold;
                }
            }
            .center{
                float: left;
                height: 32px;
                line-height: 32px;
                width: 85px;
                border-right: 1px solid #ccc;
                font-size: 12px;
                margin-left: -1px;
                text-indent: 5px;
                span{
                    color: #999;
                    margin-right: 5px;
                }
                b{
                    color:#3388bb;
                    font-weight: bold;
                }
            }
            .right{
                float: left;
                img{
                    width: 22px;
                    margin-left: 10px;
                    margin-top: 5px;
                }
            }
        }
    }
}
.end{
    @include public(60px);
    background-color: #eee;
    margin-bottom: 20px;
    text-align: center;
    img{
        height: 40px;
        margin-top: 10px;
    }
}